/* =========== HERO ================== */

main:not(.t1) header {
    display: flex;
    position: relative;
    align-items: center;
    justify-content: center;
    background-color: var(--rwgr);
    background-size: cover;
    position: relative;
    background-position-y: top;
    background-position-x: center;
    background-repeat: no-repeat;
    .container {
        padding: 4rem 5%;
        max-width: calc(1400px - 8%);
        @media screen and (min-width: 1400px) {
            padding: 4rem 0;
        }
        .row {
            margin: 0;
            h1 {
                margin-bottom: 0;
                padding: 0;
                margin-top: 0;
            }
        }
    }
    h1 {
        * {
            display: block;
            white-space: normal; // max-width: 460px;
        }
        span {}
    }
}
